import { all, call, put, take, takeLatest } from '@redux-saga/core/effects';
import axios from 'axios';
import { LOGIN_FAIL, LOGIN_REQ, LOGIN_SUCCESS } from '../REDUX_actions/types';

function loginApi(data) {
  // return axios.post('/api/login', data);
  return {
    userData: {
      email: 'test@naver.com',
      userName: 'test',
      gender: 'f',
      isAuth: true,
      image: 'image_src_path',
      isAdmin: false,
      actionUserData: data,
    },
  };
}

function* loginRequest(action) {
  const userData = yield call(loginApi, action.data);
  if (userData.userData !== null) {
    console.log(` api,    : ${userData.userData.email}`);
  }
  try {
    yield put({ type: LOGIN_SUCCESS, data: userData }); //userData   ,
    console.log(`  ${action.data.userName} ${action.data.password}`);
  } catch (err) {
    yield put({ type: LOGIN_FAIL, error: err.response.data }); // ,
    console.log(' ');
    // console.log(err.message);
  }
}
//put   :   .
function* waitLogin() {
  yield takeLatest(LOGIN_REQ, loginRequest);
  console.log(`   `);
}

function* waitlogin2() {
  while (true) {
    yield take(LOGIN_REQ, loginRequest);
  }
}

export default function* userSaga() {
  yield all([waitLogin()]);
}
